.note {
    padding:10px 10px 38px 34px;
    height: auto; 
    margin: auto;
    margin-bottom: 8px;
    width: 60%;
    font-family: 'Lato', sans-serif;
    font-size: 1.4em;
    color: #222;
    background-color: #eed; 
    border-radius: 3px;
    box-shadow: 43x 3px 2px #333;
    opacity: 1;
  }
  
  @-webkit-keyframes fadeIn { from { opacity:0; } to { opacity:0.8; } }
  
  @keyframes fadeIn { from { opacity:0; } to { opacity:0.8; } }
  @-webkit-keyframes fadeOut { from { opactiy:0.8; } to { opacity:0; } }
  @keyframes fadeOut { from { opactiy:0.8; } to { opacity:0; } }
  
  .fade-in {
      opacity:0;
      -webkit-animation:fadeIn ease-in 1;
              animation:fadeIn ease-in 1;
      -webkit-animation-fill-mode:forwards;
              animation-fill-mode:forwards;
      -webkit-animation-duration: 0.1s;
              animation-duration: 0.1s;
  }
  
  .noteContent{
    float:left;
    padding-right: 70px;
    padding-bottom: 10px;
    display: block;
  }
  
  .closebtn {
      color: #AA9;
      font-weight: bold;
      float: right;
      cursor: pointer;
      font-size: 1.3em;
  }
  
  .closebtn:hover {
      color: #AC2500;
  }
.formWrapper{
    margin: auto;
}

.noteButton{
    border: none;
    margin: 0px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    padding: 8px 24px;
    font-size: 1.3em;
    font-family: 'Droid Sans', sans-serif;
    background-color: #019875;
    color: #fff;
    transition: 0.2s ease-out;
}

.noteButton:hover{
    background-color: #02BA85;
    color: #fff;
    transition: 0.2s ease-out;
}

.noteInput{
    width: 800px;
    min-width: 800px;
    font-size: 1.3em;
    padding-left: 20px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    font-family: 'Droid Sans', sans-serif;
    background-color: rgba(34,49,63,0.8);
    color: #fff;
    transition: 0.2s ease-out;
}

.noteInput:focus{
    background-color: #E4F1FE;
    color: #111;
    transition: 0.2s ease-in;
}
